/***/ (function(module, exports) {

	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Meetup: http://www.meetup.com/papers-we-love/events/214400572/\nPaper: http://www.cs.cmu.edu/~crary/819-f09/Hoare69.pdf\nSlides: https://speakerdeck.com/paperswelove/jean-yang-on-an-axiomatic-basis-for-computer-programming\nAudio: http://www.mixcloud.com/paperswelove/jean-yang-on-an-axiomatic-basis-for-computer-programming/\n---------------------------------------------------------------------------------------------\nSponsored by The Ladders (@TheLaddersDev)\n---------------------------------------------------------------------------------------------\n\nDescription\n---------------\nOur lives now run on software. Bugs are becoming not just annoyances for software developers, but sources of potentially catastrophic failures. A careless programmer mistake could leak our social security numbers or crash our cars. While testing provides some assurance, it is difficult to test all possibilities in complex systems--and practically impossible in concurrent systems. For the critical systems in our lives, we should demand mathematical guarantees that the software behaves the way the programmer expected.\n\nA single paper influenced much of the work towards providing these mathematical guarantees. C.A.R. Hoare’s seminal 1969 paper “An Axiomatic Basis for Computer Programming” introduces a method of reasoning about program correctness now known as Hoare logic. In this paper, Hoare provides a technique that 1) allows programmers to express program properties and 2) allows these properties to be automatically checked. These ideas have influenced decades of research in automated reasoning about software correctness.\n\nIn this talk, I will describe the main ideas in Hoare logic, as well as the impact of these ideas. I will talk about my personal experience using Hoare logic to verify memory guarantees in an operating system. I will also discuss takeaway lessons for working programmers.\n\nBio\n----\nJean Yang (@jeanqasaur) is a final-year PhD student at MIT. For her PhD thesis she has created Jeeves, a programming language for automatically enforcing information flow policies for security and privacy. You may be more familiar with one of her other projects, Haskell Ryan Gosling.","meta":{"duration":"PT1H4M32S"},"presenter":{"name":null},"source":{"id":"GQi-6-d5ooQ","provider":"youtube"},"slug":"jean-yang-on-an-axiomatic-basis-for-computer-programming","tag":["Computer Programming (Conference Subject)","Mathematics (Field Of Study)","Software Engineering (Industry)","Program Correctness","Ryan Gosling","Hoare Logic","Type Checking","Computer (Industry)","Programming Language"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/GQi-6-d5ooQ/default.jpg","height":90,"width":120}},"title":"Jean Yang on An Axiomatic Basis for Computer Programming"}},{"node":{"description":"The field of rationality idea was put forward by Joseph Życiński as a context in which the questions: \"How do mathematical objects exist?\" and \"Why is mathematics so effective in the physical sciences?\" could be better understood. The idea never went beyond its seminal stage. In the present study I try to make it less fuzzy by relating it to the ontologically interpreted category theory, where \"ontological interpretation\" should be understood \"in the sense of Quine\". Roughly speaking, the ontology in the sense of Quine does not aspire to establish what does exist, but rather what a given theory or doctrine assumes there exists. To construct such an ontology, one should paraphrase a given doctrine into the first order logical calculus and look for those variables that are bound by the existential quantifiers. Only those entities that correspond to such variables are postulated to exist. However, in principle each topos has its own \"internal logic\". Consequently, we should apply Quine's program to each such category individually (by employing its own internal logic), and speak about the ontology in the sense of Quine characteristic for a given category in terms of a given logical calculus (if Quine's program is realisable in this logic). However, the Quine ontological program does not directly refer to the internal logic of a given theory, but rather to the logic with the help of which we are interpreting this theory (the external or meta-logic), and this is expected to be the standard classical logic. This is also the case with respect to category theory. When developing this theory, for instance by proving theorems, we are using standard logical laws of inference. This is why we seem entitled to ontologically interpret category theory strictly following Quine's recipe (i.e. with the help of the first order logical calculus), but we should be aware that this could be conditioned by the fact that our brain is a macroscopic object embedded into the world having the ontology characteristic for the category of sets. Having these caveats in mind I sketch \"ontological commitments\" of category theory and briefly signal some underlying philosophical problems.\n\n***\n\nThis talk was delivered during the conference ���The Limits of Physics and Cosmology” organized by Copernicus Center for Interdisciplinary Studies.\n\nThere exist various kinds of limits which are inherent in cosmological research and physics.\n\nThe most conspicuous one is connected with scientific method itself. It manifests itself during attempts to construct (or discover) suitable mathematical structures to model the physical world. However, this limit may be even more profound. Over the past 300 years we have become accustomed to the mathematical and empirical method which has proven to be uncannily successful in describing the enclosing universe. But is there a fundamental limit to the method itself which is intrinsic to the structure of the world?\n\nAnother group of limits which are more connected to ourselves – as human beings – are conceptual ones. The two monumental theories of physics of the 20th century – General Relativity and Quantum Mechanics - are incompatible and this inconsistency is not only on the mathematical level, but also on the conceptual. Usually scientists believe that the reconciliation between the relativist and quantum realms is possible. But is there a conceptual limit within ourselves as elements of the world?","meta":{"duration":"PT44M7S"},"presenter":{"name":null},"source":{"id":"jjfE2jTAQrE","provider":"youtube"},"slug":"field-of-rationality-and-category-theory-michal-heller","tag":["Centrum Kopernika Badań Interdyscyplinarnych","Centrum Kopernika","Copernicus Center for Interdisciplinary Studies","Copernicus Center","Kraków","Michael Heller (Award Winner)","Category Theory (Field Of Study)","Mathematics (Field Of Study)","Physics (Field Of Study)","Theory (Quotation Subject)","Philosophy (Field Of Study)","Józef Życiński (Deceased Person)"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/jjfE2jTAQrE/default.jpg","height":90,"width":120}},"title":"Field of Rationality and Category Theory, Michał Heller"}},{"node":{"description":"Does the Continuum Hypothesis have an answer? This question is pointed out by professor W. Hugh Woodin. He admits that 15 years ago he thought it was false. Has his opinion changed? You can see from this lecture.\n\nWilliam Hugh Woodin is an American mathematician and set theorist at Harvard University. He has made many notable contributions to the theory of inner models and determinacy. A type of large cardinal, the Woodin cardinal, bears his name.","meta":{"duration":"PT55M20S"},"presenter":{"name":null},"source":{"id":"nVF4N1Ix5WI","provider":"youtube"},"slug":"the-continuum-hypothesis-and-the-search-for-mathematical-infinity-w-hugh-woodin","tag":["Centrum Kopernika Badań Interdyscyplinarnych","Centrum Kopernika","Copernicus Center for Interdisciplinary Studies","Copernicus Center","Kraków","Continuum Hypothesis","W. Hugh Woodin","Infinity (Quotation Subject)","Mathematics (Field Of Study)","math","mathematical plationism","cardinals","Set Theory (Field Of Study)","Georg Cantor (Academic)","David Hilbert (Academic)","Kurt Gödel (Academic)"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/nVF4N1Ix5WI/default.jpg","height":90,"width":120}},"title":"The Continuum Hypothesis and the search for Mathematical Infinity, W. Hugh Woodin"}}]}},"pathContext":{"slug":"mathematics-field-of-study","tag":"Mathematics (Field Of Study)"}}

/***/ })

